<template>
  <div id="wine_shelf_manage">
    <!-- 头部 -->
    <common-header :bg="commonHeaderBg">
      <div slot="left" class="common_header_left" @click="goBack">
        <i class="icon iconfont">&#xe692;</i>
      </div>
      <div slot="right" class="common_header_right oh-1" @click="reset">{{ $t('lang.reset') }}</div>
    </common-header>
    <!-- 主体 -->
    <div class="wine_shelf_content">
      <div class="wine_shelf_wrap">
        <div class="wine_shelf_top">
          <div class="wine_img" :style="{ background: `url(${ wineImg }) no-repeat center center`, 'background-size': 'contain'}"></div>
          <h3 class="wine_text">
            <div>{{ `${ wineName }` }}</div>
            <div>{{ $t('lang.wine_shelf_manage_content') }}</div>
          </h3>
        </div>
        <div class="wine_shelf">
          <div class="region" v-if="regionData.length">
            <div
              v-for="el in regionData"
              :key="el.region"
              :class="{
                'region_item': true,
                'current_region': el.region === current.region
              }"
              @click="handleClickRegion(el.region)"
            >
              {{ el.region }}
            </div>
          </div>
          <div class="shelf" v-if="regionData.length">
            <div v-for="el in regionData" v-show="el.region === current.region" :key="el.region">
              <div v-for="item in el.shelfData" class="shelf_item" :key="item.shelfName" @click="handleClickShelf(item.shelfName)">
                <div class="wine_wrap" v-show="el.region === current.region && item.shelfName === current.shelf">
                  <img
                    v-for="(wine,i) in item.wineArr"
                    class="wine_item"
                    :src="getColorWine(wine,i)"
                    :key="i"
                    @click="handleClickWine(wine,i)"
                  />
                </div>
                <div class="shelf_bar">
                  <div class="shelf_label">{{ item.shelfName }}</div>
                  <div
                    v-if="!item.sameTotal"
                    :class="{
                      shelf_line_bar: true,
                      current_bar: el.region === current.region && item.shelfName === current.shelf
                    }"
                  >
                  </div>
                  <div v-if="item.sameTotal" class="shelf_content">
                    <div
                      :class="{
                        circle: true,
                        current_bar: el.region === current.region && item.shelfName === current.shelf
                      }"
                    >
                    </div>
                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#iconicon-wine_position"></use>
                    </svg>
                    <p>x</p>
                    <p class="value" v-text="item.sameTotal"></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 底部按钮 -->
    <common-bottom :text="$t('lang.remove')" @btnHandle="handleRemove" :disabledBtn="!modifyDataList.length">
      <span slot="bottom_text" v-show="modifyDataList.length">
        {{ modifyDataList.length > 0 ? modifyDataList.length : 0 }} {{ modifyDataList.length > 1 ? $t('lang.bottles_d') : $t('lang.bottle_d') }}
      </span>
    </common-bottom>
    <!-- loading -->
    <loading :show-loading="showLoading"></loading>
  </div>
</template>
<script type="text/ecmascript-6">
  import imageAll from "./imageData"
  import defaultRegionData from "./defaultRegionData.json"
  export default {
    name: 'wineShelfManage',
    data(){
      return {
        nativeKey: 'wineShelfManage',
        from: 'html',
        regionData: defaultRegionData.data.regionData,
        modifyDataList: [],
        initialRegionData: JSON.parse(JSON.stringify(defaultRegionData.data.regionData)),
        wineImg: this.$store.state.inventory.inventoryToPositionDto.imageUrl || '',
        wineName: this.$store.state.inventory.inventoryToPositionDto.name || '',
        showLoading: true,
        current: {
          region: 'A',
          shelf: ''
        }
      }
    },
    created(){
      this.nativeKey = this.$route.name || 'wineShelfManage'
      this.$i18n.locale = this.$commonMethods.getNativeLanguage(this.$route.query.language,this.hasLanguage,this.lan)
      this.from = this.$route.query.from || 'html'
      this.getAllWinesInDevice()
      this.showFirstShelf(this.regionData)
      window.getAllWinesInDeviceCallback = this.getAllWinesInDeviceCallback
      window.removeWineWithIdArrayCallback = this.removeWineWithIdArrayCallback
	    window.onBackPressedCallback = this.onBackPressedCallback
    },
    computed: {
      commonHeaderBg(){
        return { 'background': '#e9e9e9' }
      }
    },
    methods: {
      //获取酒架酒信息
      getAllWinesInDevice(){
        this.showLoading = true
        const model = this.$store.state.inventory.inventoryToPositionDto;
        console.log("this.model",model)
        const shelfString = JSON.stringify({ wineInfo: model })
        this.$native.getAllWinesInDevice(this.from,this.nativeKey,model.deviceId,shelfString)
        if(this.from == 'html'){
          this.getAllWinesInDeviceCallback({ resultCode: 0, data: { regionData: JSON.parse(JSON.stringify(this.initialRegionData)) } })
        }
      },
      //获取酒架酒信息回调
      getAllWinesInDeviceCallback(res){
        this.showLoading = false
        if(res.resultCode == 0 || res.resultCode == '0') {
          if('data' in res){
            let { regionData } = res.data
            this.showFirstShelf(regionData)
            this.initialRegionData = JSON.parse(JSON.stringify(regionData))
            this.regionData = regionData
          }
        }else{
          this.$toast(this.$t('lang.failure'))
        }
      },
      //初次展示的货架
      showFirstShelf(arr){
        arr.find(el => {
          const res = el.shelfData.filter(item => { return item.sameTotal })
          if(res.length){
            this.current.region = el.region
            if(res.length === 1){
              this.current.shelf = res[0].shelfName
            }
            return res
          }
        })
      },
      //返回到库存详情
      goBack(){
        this.$commonMethods.routerGo(this.$router, '/inventoryDetail', this.$route.query)
      },
      //重置功能
      reset(){
        this.regionData = JSON.parse(JSON.stringify(this.initialRegionData))
        this.modifyDataList.splice(0)
        this.modifyDataList = []
      },
      //展示瓶子图片路径
      getColorWine(wine,i){
        let c
        if(wine.isThisType === "true"){
          c = "grey"
        }else if(wine.isThisType === "black"){
          c = "black"
        }else if(wine.isThisType === "false" || wine.isThisType === "no"){
          c = "white"
        }
        return imageAll[`${c}WineImage${i+1}`]
      },
      //瓶子点击
      handleClickWine(wine){
        if(wine.isThisType === "true"){
          wine.isThisType = "black"
          const obj = {
            position: wine.wineInventoryDtoModel.exactPosition,
            idInventory: wine.wineInventoryDtoModel.idInventory,
            ...this.current
          };
          this.modifyDataList.push(obj)
        }else if(wine.isThisType === "black"){
          wine.isThisType = "true"
          const i = this.modifyDataList.findIndex(el => el.region === this.current.region && el.shelf === this.current.shelf && el.position === wine.wineInventoryDtoModel.exactPosition)
          this.modifyDataList.splice(i,1)
        }
      },
      //切换货架
      handleClickShelf(shelf){
        this.current.shelf = shelf
      },
      //切换ABC温区
      handleClickRegion(region){
        this.current.region = region
      },
      //删除酒
      removeWineWithIdArray(removeWineString){
        this.showLoading = true;
        this.$native.removeWineWithIdArray(this.from,this.nativeKey,removeWineString)
        if(this.from == 'html'){
          this.removeWineWithIdArrayCallback()
        }
      },
      //删除酒回调
      removeWineWithIdArrayCallback(res){
        this.showLoading = false
        this.goBack()
      },
      //底部删除按钮
      handleRemove(){
        const arr = this.modifyDataList.map(el => el.idInventory)
        this.removeWineWithIdArray(JSON.stringify(arr))
      },
      //物理返回键 
	    onBackPressedCallback(){
		    this.goBack()  
	    }
    },
    components: {
      commonHeader: () => import('@/components/common/commonHeader'), 
      commonBottom: () => import('@/components/common/commonBottom'),
      loading: () => import('@/components/common/loading')
    }
  }
</script>

<style lang="less" src="./index.less" scoped></style>



